.App {
    position: relative;
    font-family: 'Montserrat', sans-serif;
  min-height: 100%;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.container {
  max-width: 1170px;
  margin: 0px auto;
  padding: 0px 15px;
}

.hide {
  display: node;
}

a {
  text-decoration: none;
}

.block-img {
  display: block;
  position: relative;
  margin: 0px auto;
}

.block-img img,
.block-img_small img  {
  max-height: 100%;
  width: 100%;
}

.block-img_small {
  display: block;
  width: 50%;
  height: max-content;
  position: relative;
  margin: 0px auto;
  margin-bottom: 30px;
}


.close_white,
.close_black {
    position: absolute;
    right: 20px;
    top: 20px;
    width: 30px;
    height: 30px;
    border: 1px solid #000;
    border-radius: 50%;
    cursor: pointer;
}

.close_white {
    border:  1px solid #fff;
}

.close_white:before,
.close_white:after,
.close_black:before,
.close_black:after {
    content: '';
    position: absolute;
    width: 20px;
    height: 2px;
    left: 5px;
    top: 14px;
}

.close_white:before,
.close_black:before {
    transform: rotate(45deg);
}

.close_white:after,
.close_black:after {
    transform: rotate(-45deg);
}

.close_white:before,
.close_white:after {
    background-color: #fff;
}

.close_black:before,
.close_black:after {
    background-color: #000;
}




@media (max-width:1200px){
  .container {
    max-width: 970px;
  }
}

@media (max-width:992px){
  .container {
    max-width: 750px;
  }
}

@media (max-width:767px){
  .container {
    max-width: 100%;
  }
}