.habits {
    min-height: 80vh;
    position: relative;
    padding-top: 50px ;
}

.habits__hide {
    min-height: 80vh;
    position: relative;
    padding-top: 50px ;
}

.habits-block {
    position: relative;
}

.habits-block__title {
    font-size: 24px;
    font-weight: 700;
    margin-bottom: 30px;
    margin-top:10px;
}

.habits-block__button_blue {
    position: absolute;
    right: 0;
    top: 0;
    background: #0038FF;
    cursor: pointer;
    color: #fff;
    font-size: 20px;
    z-index: 3;
    border: none;
    padding: 8px 20px;
}

.habits-block__button_blue_small {
    position: absolute;
    right: 30px;
    bottom: 20px;
    background: #0038FF;
    cursor: pointer;
    color: #fff;
    font-size: px;
    z-index: 3;
    border: none;
    padding: 8px 10px;
}

.habit {
    position: relative;
    display: block;
    margin-bottom: 20px;
}

.habit__main {
    position: relative;
    display: flex;
    justify-content: space-between;
    background: #FFFFFF;
    box-shadow: 0px 2px 10px 3px rgba(0, 0, 0, 0.12);
    padding: 20px 30px;
    margin-bottom: 10px;
}

.habit__check {
    position: absolute;
    left: 30px;
    top: 16px;
    height: 25px;
    width: 25px;
    z-index: 3;
}

.habit__name {
    padding-left: 35px;
    position: relative;
}

.habit__info-button {
    position: relative;
    height: 20px;
    width: 40px;
    background-color: transparent;
}

.habit__info-button::after {
    content: '';
    height:2px;
    background-color: #000;
    width: 20px;
    position: absolute;
    left: 13px;
    top:10px;
    transform: rotate(-45deg);
    transition: .3s ease all;
}

.habit__info-button::before {
    content: '';
    height:2px;
    background-color: #000;
    width: 20px;
    position: absolute;
    left: 0px;
    top:10px;
    transform: rotate(45deg);
    transition: .3s ease all;
}

.habit__info-button.active::after {
    transform: rotate(+45deg);
}

.habit__info-button.active::before {
    transform: rotate(-45deg);
}

.habit__info {
    position: relative;
    display: flex;
    justify-content: space-between;
    
}

.history {
    width: 60%;
    position: relative;
    background: #FFFFFF;
    box-shadow: 0px 2px 10px 3px rgba(0, 0, 0, 0.12);
    padding: 20px 30px;
    margin-bottom: 20px;
    margin-right: 10px;
}

.history__title {
    margin-bottom: 30px;
    position: relative;
    display: flex;
    flex-direction: row;
}

.history__title  span {
   margin-left: auto;
}

.history__title .history__block_check,
.history__title .history__block_uncheck {
    margin-right: 5px;
} 

.history-rows {
    position: relative;
    width: fit-content;
    margin: 0px auto;
}

.history-rows__row {
    margin: 10px 0px;
    position: relative;
    display: flex;
}

.history__activity {
    width: 40%;
    position: relative;
    background: #FFFFFF;
    box-shadow: 0px 2px 10px 3px rgba(0, 0, 0, 0.12);
    padding: 20px 30px;
    margin-bottom: 20px;
}

.history__block_check {
    position: relative;
    height: 22px;
    width: 22px;
    background-color: #0038FF;
    border: none;
    margin: 0px 20px 0px 0px;
}

.history__block_uncheck {
    position: relative;
    height: 20px;
    width: 20px;
    border: 1px solid #0038FF;
    margin: 0px 20px 0px 0px;
}

.history__block_today {
    position: absolute;
    font-size: 10px;
    top:-15px;
    left: -3px;
}

.history__diagram {
    margin: 10px 50px;
    position: relative;
    display: flex;
    /* flex-direction: row-reverse; */
    height: 160px;
    align-items: flex-end; 
}

.history__column {
    width: 14.4% ;
    background-color: #0038FF;
    border: 1px solid #000;
    bottom: 0;
}

/* modal-window */
.modal-wrap {
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: #f5f5f5;
    z-index: 5;
    top: 0;
    left: 0;
    opacity: .8;
}
 
.modal-window {
     width: 300px;
     position: absolute;
     left: 50%;
     top: 30%;
     transform: translate(-50%,-30%);
     background-color: rgb(37, 26, 26);
     text-align: center;
     border-radius: 10px;
     z-index: 6;
     padding: 30px;
     color: #fff;
}
 
.modal-window__message {
     position: relative;
}
 
.modal-window__button {
     padding: 10px 40px;
     border:none;
     cursor: pointer;
     color: #fff;
     border-radius: 5px;
     background-color: #f39422;
     margin: 30px 10px auto;
}

.modal-window__button:hover {
    opacity: 1;
}

.modal-window__button:active {
    background-color: #fcbd70;
}

.modal-window .close_white ~ .modal-window__message{
    margin-top: 40px;
}
/* form */

.habit-form {
    width: 320px;
   position: absolute;
   padding: 50px 40px 20px;
   border-radius: 5px;
   left: 50%;
   top:40%;
   transform: translate(-50%,-40%);
  color: #000;
  background-color: #fff;
  box-shadow: 0px 2px 10px 3px rgba(0, 0, 0, 0.12);
  z-index: 20;
}

.form__group {
    position: relative;
    margin-bottom: 30px;
}

.form__label {
    position: absolute;
    top: 5px;
    left: 0px;
    transition: .3s;
    font-size: 18px;
    z-index: 1;
}

.form__input,
.form__textarea {
    border:none;
    position: relative;
    width: 95%;
    padding: 10px;
    border-bottom: 1px solid  rgba(110, 109, 109, 0.3);
    background-color: transparent;
    outline: none;
    color: rgb(0, 0, 0);
    transition: .5s;
    z-index: 2;
    font-size: 18px;
}

.form__input:focus {
    border-bottom: 1px solid #000;
}

.form__input:focus ~ .form__label,
.form__input:not(:placeholder-shown) ~ .form__label
{
    font-size: 12px;
    top: -10px;
}

.form__textarea:focus ~ .form__label,
.form__textarea:not(:placeholder-shown) ~ .form__label {
    font-size: 12px;
    top: -15px;
}

.form__submit_blue {
   width: 127px;
   height: 50px;
   text-align: center;
    border:none;
    cursor: pointer;
    color: #fff;
    border-radius: 5px;
    background-color: #0038FF;
    transition: .3s ease;
    margin: 0px auto;
    margin-top: 20px;
    display: block;
    font-size: 18px;
}

.form__submit_blue:hover {
    opacity: .9;
}

.form__title {
    font-size: 18px;
    margin-bottom: 10px;
}

.form__group_center {
    width: 120px;
    margin: 10px auto;
}

.form__input_radio {
    margin-right: 10px;
}

.form__message {
    position: absolute;
    bottom: -23px;
    font-size: 14px;
    left: 0;
    color: #CD2443;
}

@media (max-width: 767px){
    .modal-window {
        width: max-content;
    }

    .habits {
        padding-top: 60px;
        min-height: 50vh;
    }

    .habits-block__title {
        font-size: 20px;
        margin-bottom: 20px;
    }

    .habits-block__button_blue {
        position: absolute;
        font-size: 16px;
        left: 0;
        transform: translate(0px, -50px);
    }

    .habit {
        width: 100%;
    }

    .habit__info {
        display: block;
    }

    .history ,
    .history__activity {
        width: auto;
    }

    .history {
        margin: 0px 0px 10px 0px ;
    }

    .history__diagram {
        margin:0px;
    }

    .history__block_uncheck,
    .history__block_check {
        margin-right: 10px;
    }

    .weekly-habits .history__title {
        margin-bottom: 35px;
    }

    .habit-form {
        width: 60%;
    }

    .history__title {
        font-size: 14px;
    }
}