.footer {
    width: 100%;
    background-color: #2F2929;
    position: relative;
    padding: 25px 0px;
}

.header__logo_white {
    font-size: 36px;
    font-weight: 700;
    color: #fff;
    margin-bottom: 30px;
}

.footer__info_title_right {
    font-size: 26px;
    color: #756565;
    margin-bottom: 15px;
    text-align: right;
}

.footer__info {
    font-size: 24px;
    color: #fff;
    margin-bottom: 10px;
}

.footer__info:hover,
.footer__info_right:hover {
    opacity: .7;
    cursor: pointer;
}

.footer__info_right {
    display: block;
    font-size: 24px;
    color: #fff;
    margin-bottom: 10px;
    text-align: right;
}

.footer__social-media {
    width: 200px;
    margin: 0px auto;
    display: flex;
    margin-top: 25%;
    transform: translate(0, -50%);
}

.footer__social-media .block-img:hover {
    opacity: .8;
    transform: translate(0, -3%);
    cursor: pointer;
}

@media (max-width: 767px){
    .header__logo_white {
        font-size: 28px;
        margin-bottom: 15px;
    }

    .footer .columns-3 {
        display: flex;
        flex-direction: column;
        justify-content: stretch;
        text-align: center;
    }

    .footer__info {
        font-size: 18px;
    }

    .footer__info_right {
        text-align: center;
        font-size: 18px;
    }

    .footer__info_title_right {
        text-align: center;
    }

    .footer .columns-3__column {
        display: block;
    }

    .footer__social-media {
        width: 150px;
        margin: 10px  auto 15px;
        transform: translate(0, 0);
    }

    .footer__social-media .block-img {
        margin-right: 10px;
    }
}