.authorisation {
    height: 100vh;
    width: 100%;
    background-color: rgb(238, 232, 225);
    z-index: 15;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.sign-in, .sign-up{
  position: relative;
    z-index: 3;
    width: 400px;
    padding: 20px 40px;
    background-color: #fff;
    font-size: 16px;
    height: auto;
    margin-top: -100px;
}

.validation-message {
    position: absolute;
    width: 91%;
    transition: all 2s ease;
    text-align: center;
    left: 0;
    padding: 20px;
    bottom:-100px;
    background-color: #fff;
}

.sign-in  input[type=text], .sign-up input[type=text], 
.sign-in input[type=password], .sign-up input[type=password] {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    box-sizing: border-box;
    font-size: 14px;
   
}
  
.sign-in  button,
.sign-up button {
    background-color: #345be7;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 100%;
    font-size: 18px;
}
  
button:hover {
    opacity: 0.9;
}

.imgcontainer {
    text-align: center;
    margin: 0px 0 12px 0;
    font: 1em sans-serif;
} 

img.avatar {
    width: 40%;
}

.sign-in .switch-link,
.sign-up .switch-link {
    display: flex;
    justify-content: center;
    padding: 5px 0px;
}

.switch-link p ~ p {
    font-weight: 700;
    cursor: pointer;
    margin-left: 10px;
    position: relative;
}

.switch-link p img {
    height: 20px;
    width: 20px;
    position: absolute;
    right: -20px;
    top:2px;
}

.hide  {
    display: none;
}

#show-pass {
    width: 20px;
    height: 20px;
    margin-right: 8px;
    margin-top: 5px;
}

.group {
     position: relative;
}

.show-pass-img {
    width: 20px;
    height: 20px;
    position: absolute;
    right: 10px;
    top:18px;
}

@media (max-width: 992px){
    .sign-in, .sign-up {
        width: 300px;
    }
}

@media (max-width: 767px){
    .sign-in, .sign-up {
        width: 100%;
        margin: 20px;
        margin-top: -20px;
    }

    .sign-in .switch-link, .sign-up .switch-link {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .switch-link a {
        margin-left: -15px;
    }

    .show-pass-img {
        top: 18px;
    }
}