.home {
    width: 100%;
    position: relative;
}

.article {
    width: 100%;
    color: #000000;
    text-align: center;
}

.article__title {
    font-family: Inter;
    font-weight: bold;
    font-size: 38px;
    line-height: 58px;
}

.article__title_small {
    font-size: 24px;
    font-weight: 700;
    margin-bottom: 20px;
}

.article__subtitle_small {
    font-size: 18px;
}

.article__subtitle {
    margin-bottom: 28px;
    font-size: 24px;
    line-height: 29px;
}

.intro {
    height: 90vh;
    position: relative;
    margin-top: 100px;
}

.intro__button_blue {
    background: #0038FF;
    box-shadow: 0px 9px 35px rgba(0, 0, 0, 0.45);
    border-radius: 10px;
    position: absolute;
    left:50%;
    transform: translate(-50%,0);
    cursor: pointer;
    color: #fff;
    font-size: 20px;
    z-index: 3;
    border: none;
    padding: 10px 50px;
}

.intro__button_blue:hover {
    opacity: 0.9;
    box-shadow: 0px 19px 55px rgba(0, 0, 0, 0.45);
    transform: translate(-50%,-2%);
}

.intro__img {
    height: 70%;
    width: 70%;
}

.row {
    margin-bottom: 30px;
}

.columns-3 {
    position: relative;
    display:flex;
    justify-content:space-between;

}

.columns-3__column {
    width: 30%;
    position: relative;
}

.columns-3__column {
    width: 50%;
    margin: 0px auto;
}

.instruction {
    margin-bottom: 100px;
}

@media (max-width:767px){
    
    .intro {
        height: auto;
        margin-top: 30px;
    }

    .article__title {
        font-size: 26px;
    }

    .article__subtitle {
        font-size: 16px;
        margin-bottom: 15px;
    }

    .intro__button_blue {
        width: 40%;
        padding: 8px;
        text-align: center;
        margin: 0px auto;
        font-size: 16px;
    }

    .intro__img {
        margin-top: 50px;
        width: auto;
    }

    .columns-3 {
        display: block;
    }

    .columns-3__column {
        width: 100%;
        display: flex;
    }

    .columns-3__column .article {
        margin-left: 30px;
    }

    .article__title_small {
        font-size: 18px;
        text-align: left;
        margin-bottom: 10px;
    }

    .article__subtitle_small {
        font-size: 16px;
        text-align: left;
        margin-bottom: 10px;
        padding-left: 20px;
    }

    .row {
        margin-bottom: 10px;
    }

    .instruction {
        margin-bottom: 40px;
    }
}