.header {
    background-color: #fff;
    padding: 20px;
    position: relative;
    text-align: center;
    box-shadow: 0px 6px 12px 1px rgba(0, 0, 0, 0.25);
}

.header__body {
    width: 100%;
    display: flex;
}

.header__menu {
    width: 100%;
    position: relative;
}

.header__link {
    font-size: 24px;
    line-height: 29px;
    color: #000000;
    cursor: pointer;
}

.header__list {
    display: flex;
    width: 100%;
}

.header ul li {
    min-width: 100px;
    list-style-type: none ;
}

.header__logo_black {
    display: inline;
    font-size: 36px;
    font-weight: 700;
    margin-top: -12px;
    left: 0;
    color: #000;
    z-index: 10;
    margin-right: 20px;
}

.header__sign-in {
    margin-left: auto;
}

.header__burger {
    display: none;
}

.header__burger.active::before {
    transform: rotate(45deg);
    top: 25px;
}

.header__burger.active::after {
    transform: rotate(-45deg);
    bottom: 22px;
}

.header__burger.active span {
    transform: scale(0);
}

@media (max-width:767px){
    .header__body {
        justify-content: space-between;
    }

    .header__logo_black {
        margin-top: 10px;
        font-size: 28px;
    }

    .header__burger {
        display: block;
        position: relative;
        width: 50px;
        height: 50px;
        z-index: 10;
        border: 1px solid black;
        border-radius: 50%;
    }

    .header__burger span {
        position: absolute;
        background-color: rgb(0, 0, 0);
        width: 26px;
        height: 3px;
        top: 24px;
        left: 12px;
        transition: all 0.3s ease;
    }

    .header__burger:before,
    .header__burger:after {
        content: '';
        background-color: rgb(0, 0, 0);
        position: absolute;
        width: 26px;
        height: 3px;
        left: 12px;
        transition: all 0.3s ease;
    }

    .header__burger:before {
        top: 10px;
    }

    .header__burger:after {
        bottom: 10px;
    }

    .header__menu {
        top: -100%;
        position: fixed;
        width: 100%;
        left: 0;
        z-index: 9;
        padding: 110px 0px 20px 0px;
        background-color: #fff;
        margin-top: -30px;
        transition: all .6s ease 0s;
        box-shadow: 0px 6px 12px 1px rgba(0, 0, 0, 0.25);
    }
    .header__menu.active {
        top: 0;
    }

    .header__list {
        display: block;
    }

    .header__list li {
        margin-bottom: 20px;
    }
}